@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
* {
  padding: 0;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
}

body{
  position: relative;
  background-color: rgb(235, 234, 232);
}

button {
  cursor: pointer;
}

input {
  border-radius: 0;
}

input[type="search"] {
  -webkit-appearance: none;
}

@font-face {
  font-family: 'icomoon';
  src:  url(/react-todo-app/static/media/icomoon.a09cf6e8.eot);
  src:  url(/react-todo-app/static/media/icomoon.a09cf6e8.eot) format('embedded-opentype'),
    url(/react-todo-app/static/media/icomoon.b10e6ab9.ttf) format('truetype'),
    url(/react-todo-app/static/media/icomoon.e7467838.woff) format('woff'),
    url(/react-todo-app/static/media/icomoon.479cbfbe.svg) format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-info:before {
  content: "\E902";
}
.icon-arrow-back:before {
  content: "\E901";
}
.icon-add-sign:before {
  content: "\E900";
}
.icon-settings:before {
  content: "\E994";
}
.icon-cancel-circle:before {
  content: "\EA0D";
}


